import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PurchaseReceiptUpdateForm from "./updateForm";
import PurchaseReceiptDocUpdateForm from "./updateDocForm";
import HistoryPurchaseReceipt from "./history";

import notification from "../../../../components/notification";
import { Spin, Tooltip, Tabs } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/purchase/purchaseReceipt/actions";
import { Box } from "../../../App/app.style";
import Print from "./print";

const redirecTo = "/dashboard/purchases/purchase-receipts";
const TabPane = Tabs.TabPane;

class UpdatePurchaseReceipt extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editPurchaseReceipt } = this.props;
    const { id } = this.props.match.params;
    editPurchaseReceipt(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseReceipt.error) {
      notification("error", this.props.purchaseReceipt.error);
      this.props.clearError();
    }
    if (this.props.purchaseReceipt.success) {
      //   this.props.history.replace(
      //     redirecTo + "/update/" + this.props.purchaseReceipt.id_purchase_receipt
      //   );
      // this.props.history.replace(redirecTo);
      notification("success", this.props.purchaseReceipt.success);
      this.props.clearSuccess();
    }
    // if (this.props.purchaseReceipt.warning) {
    //   // this.props.history.replace(
    //   //   redirecTo + "/update/" + this.props.purchaseReceipt.id_purchase_order
    //   // );
    //   //   this.props.history.replace(redirecTo);
    //   notification("warning", this.props.purchaseReceipt.warning);
    //   this.props.clearSuccess();
    // }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updatePurchaseReceipt } = this.props;
    const { id } = this.props.match.params;
    updatePurchaseReceipt(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelPurchaseReceipt } = this.props;
    const { id } = this.props.match.params;

    cancelPurchaseReceipt(id, value);
  };

  handleSubmitReject = value => {
    const { rejectPurchaseReceipt } = this.props;
    const { id } = this.props.match.params;

    rejectPurchaseReceipt(id, value);
  };

  render() {
    // console.log(this.props);
    const { loading, loadingSubmit } = this.props.purchaseReceipt;
    const purchaseReceipt = this.props.purchaseReceipt.purchaseReceipt
      ? this.props.purchaseReceipt.purchaseReceipt
      : [];
    const { printData } = this.props.purchaseReceipt;
    const modified = { ...purchaseReceipt, ...printData };

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="purchaseReceipts.update.title" />}
            marginBottom={true}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={modified} />

            <Button
              onClick={() => this.props.history.replace(redirecTo)}
              style={{ marginLeft: "5px" }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryPurchaseReceipt
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={purchaseReceipt ? purchaseReceipt.description : null}
          />

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              {loading && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <PurchaseReceiptUpdateForm
                  submit={this.handleSubmit}
                  purchaseReceipt={this.props.purchaseReceipt}
                  handleSubmitCancel={this.handleSubmitCancel}
                  handleSubmitReject={this.handleSubmitReject}
                />
              )}
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
            >
              {loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <PurchaseReceiptDocUpdateForm
                  submit={this.handleSubmit}
                  purchaseReceipt={this.props.purchaseReceipt}
                  params={this.props.match.params}
                />
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseReceipt: state.purchaseReceipt,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePurchaseReceipt);
