const actions = {
  getPurchaseReceipts: (page, search, filter) => ({
    type: "GET_PURCHASE_RECEIPTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPurchaseReceipt: () => ({
    type: "CREATE_PURCHASE_RECEIPT_REQUEST"
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_FILTER_PO_PURCHASE_RECEIPT_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storePurchaseReceipt: data => ({
    type: "STORE_PURCHASE_RECEIPT_REQUEST",
    payload: { body: data }
  }),
  editPurchaseReceipt: (id_purchase_receipt, search) => ({
    type: "EDIT_PURCHASE_RECEIPT_REQUEST",
    payload: { id_purchase_receipt: id_purchase_receipt, search: search }
  }),
  updatePurchaseReceipt: (id_purchase_receipt, data) => ({
    type: "UPDATE_PURCHASE_RECEIPT_REQUEST",
    payload: {
      id_purchase_receipt: id_purchase_receipt,
      body: data
    }
  }),
  showPurchaseReceipt: id_purchase_receipt => ({
    type: "SHOW_PURCHASE_RECEIPT_REQUEST",
    payload: { id_purchase_receipt: id_purchase_receipt }
  }),
  confirmPurchaseReceipt: id_purchase_receipt => ({
    type: "CONFIRM_PURCHASE_RECEIPT_REQUEST",
    payload: { id_purchase_receipt }
  }),
  rejectPurchaseReceipt: (id_purchase_receipt, data) => ({
    type: "REJECT_PURCHASE_RECEIPT_REQUEST",
    payload: { id_purchase_receipt, body: data }
  }),
  cancelPurchaseReceipt: (id_purchase_receipt, data) => ({
    type: "CANCEL_PURCHASE_RECEIPT_REQUEST",
    payload: { id_purchase_receipt, body: data }
  }),
  uploadDocument: (id_purchase_receipt, data) => ({
    type: "UPLOAD_PURCHASE_RECEIPT_DOCUMENT_REQUEST",
    payload: {
      id_purchase_receipt,
      body: data
    }
  }),
  downloadDocument: id_purchase_receipt_document => ({
    type: "DOWNLOAD_PURCHASE_RECEIPT_DOCUMENT_REQUEST",
    payload: { id_purchase_receipt_document }
  }),
  deleteDocument: (id_purchase_receipt_document, id_purchase_receipt) => ({
    type: "DELETE_PURCHASE_RECEIPT_DOCUMENT_REQUEST",
    payload: {
      id_purchase_receipt_document: id_purchase_receipt_document,
      id_purchase_receipt: id_purchase_receipt
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_PURCHASE_RECEIPT",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
