import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import { getUnitConversion } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      dataSource: [...items],
      count: items.length + 1,
      visible: false,
      importedTransaction: 0
    };
  }

  componentDidUpdate() {
    const { loadingTransactions, status, items, clearItems } = this.props;

    if (!loadingTransactions && items.length !== 0) {
      let newData = [];
      let newTotQty = 0;
      let newTotRecQty = 0;
      let newTotLeftQty = 0;
      let newIndex = 0;
      let importedTransaction = 0;

      items.map(item => {
        // counting imported transaction
        // untuk menentukan transaction_date
        importedTransaction++;

        // data unit po untuk get unit
        const unit_conversion = item.unit_conversion
          ? item.unit_conversion
          : item.item.unit_conversion;
        const id_unit_po = item.id_unit_po
          ? item.id_unit_po
          : item.po_detail
          ? item.po_detail.id_unit
          : item.id_unit;
        const po_unit_conv = getUnitConversion(id_unit_po, unit_conversion);

        let init = {};
        init.index_no = item.index_no;
        init.id_purchase_order_detail = item.id_purchase_order_detail;
        init.id_item = item.id_item;
        init.item_code = item.item_code;
        init.item_name = item.item_name;
        init.received_qty = item.received_qty
          ? parseFloat(item.received_qty)
          : item.po_detail
          ? parseFloat(item.po_detail.received_qty)
          : 0;
        init.left_qty = item.left_qty ? item.left_qty : 0;

        // data unit po untuk get unit
        init.id_unit_po = id_unit_po;
        init.po_unit_conv = po_unit_conv;

        init.ordered_qty = item.ordered_qty
          ? item.ordered_qty
          : parseFloat(item.qty);
        init.base_ordered_qty = item.base_ordered_qty
          ? item.base_ordered_qty
          : parseFloat(init.ordered_qty) * po_unit_conv;
        init.receive_qty = item.receive_qty
          ? item.receive_qty
          : init.ordered_qty - init.received_qty;

        init.unit_conversion = unit_conversion;
        init.id_unit = item.unit ? item.unit.id_unit : item.id_unit;
        init.unit_code = item.unit ? item.unit.unit_code : item.unit_code;

        init.id_base_unit = item.base_unit
          ? item.base_unit.id_unit
          : item.id_base_unit;
        init.base_unit_code = item.base_unit
          ? item.base_unit.unit_code
          : item.base_unit_code;

        init.receipt_closed = item.po_detail
          ? item.po_detail.receipt_closed
          : 0;
        init.lock =
          // parseFloat(currData.left_qty) === 0 ||
          (status && status !== 1) || init.receipt_closed === 1 ? true : false;

        init.unit_conv = getUnitConversion(init.id_unit, init.unit_conversion);

        init.base_qty = parseFloat(init.receive_qty) * init.unit_conv;

        newTotQty += parseFloat(init.base_ordered_qty);
        newTotRecQty += parseFloat(init.base_qty);
        newTotLeftQty += parseFloat(init.left_qty) * init.unit_conv;
        newIndex = parseInt(init.index_no) + 1;

        newData.push(init);
      });
      // console.log(items);
      this.setState({
        dataSource: [...newData],
        count: newIndex,
        importedTransaction
      });

      clearItems(newData, newTotQty, newTotRecQty, newTotLeftQty, importedTransaction);
    }
  }

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    let newTotQty = 0;
    let newTotRecQty = 0;
    let newTotLeftQty = 0;
    let importedTransaction = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotQty += parseFloat(currData.base_ordered_qty);
      newTotRecQty += parseFloat(currData.base_qty);
      newTotLeftQty += parseFloat(currData.left_qty) * currData.unit_conv;

      // counting imported transaction
      // untuk menentukan transaction_date
      importedTransaction++;
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      importedTransaction
    });
    handleSaveItemList(newData, newTotQty, newTotRecQty, newTotLeftQty, importedTransaction);
  };

  handleSave = row => {
    const { handleSaveItemList } = this.props;
    const newData = this.state.dataSource;
    const { importedTransaction } = this.state;
    let newTotQty = 0;
    let newTotRecQty = 0;
    let newTotLeftQty = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    row.base_qty = parseFloat(row.receive_qty) * row.unit_conv;

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    newData.map(currData => {
      newTotQty += parseFloat(currData.base_ordered_qty);
      newTotRecQty += parseFloat(currData.base_qty);
      newTotLeftQty += parseFloat(currData.left_qty) * currData.unit_conv;
    });

    // console.log(row);

    this.setState({ dataSource: [...newData] });
    handleSaveItemList(newData, newTotQty, newTotRecQty, newTotLeftQty, importedTransaction);
  };

  render() {
    // console.log(this.props.purchaseOrder);

    const { dataSource } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const raw_columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "10%",
        render: text => {
          if (text && text.length > 10) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 10) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="purchaseOrderQuantity" />}>
            <a href="javascript:;"><IntlMessages id="poQty" /></a>
          </Tooltip>
        ),
        dataIndex: "ordered_qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => <><NumberFormat value={text} /> {row.unit_code}</>
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="purchaseOrderBaseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="poBaseQty" /></a>
          </Tooltip>
        ),
        dataIndex: "base_ordered_qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => <><NumberFormat value={text} /> {row.base_unit_code}</>
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="receivedQty" />}>
            <a href="javascript:;"><IntlMessages id="rcvdQty" /></a>
          </Tooltip>
        ),
        dataIndex: "received_qty", // dari PO
        width: "15%",
        align: "right",
        render: (text, row) => <><NumberFormat value={text} /> {row.unit_code}</>
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="leftQuantity" />}>
            <a href="javascript:;"><IntlMessages id="leftQty" /></a>
          </Tooltip>
        ),
        dataIndex: "left_qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => <><NumberFormat value={text} /> {row.unit_code}</>
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="receiveQuantity" />}>
            <a href="javascript:;"><IntlMessages id="receiveQty" /></a>
          </Tooltip>
        ),
        dataIndex: "receive_qty", // dari PR
        width: "15%",
        align: "right",
        editable: true
      },
      {
        title: getLanguage({ id: "unit" }),
        width: "10%",
        dataIndex: "unit_code",
        align: "center"
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="baseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="baseQty" /></a>
          </Tooltip>
        ),
        dataIndex: "base_qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => <><NumberFormat value={text} /> {row.base_unit_code}</>
      },
      
    ];

    let operation = 
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          return (
            <Popconfirm
              title={<IntlMessages id="confirmDelete" />}
              onConfirm={() => this.handleDelete(row.index_no)}
              okText={<IntlMessages id="yes" />}
              cancelText={<IntlMessages id="no" />}
            >
              <Tooltip
                title={<IntlMessages id="delete" />}
              >
                <Button
                  //style={{ borderRadius: "50%" }}
                  className="invoiceDltBtn"
                  size="small"
                >
                  <i className="ion-trash-b" />
                </Button>
              </Tooltip>
            </Popconfirm>
          );
        }
      }

    if ((!this.props.status || this.props.status === 1) && raw_columns.length === 10) {
      raw_columns.push(operation)
    }


    const columns = raw_columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          lock: record.lock,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          taxes: this.props.taxes,
          handleSave: this.handleSave
        })
      };
    });
    return (
      <CardWrapper>
        <Scrollbars style={{ width: "100%" }}>
          <TableWrapper
            className="invoiceListTable"
            components={components}
            rowKey={record => record.index_no}
            rowClassName={() => "editable-row"}
            dataSource={dataSource}
            columns={columns}
            pagination={false}
            // scroll={{ x: 1300 }}
          />
        </Scrollbars>
      </CardWrapper>
    );
  }
}

export default EditableTable;
