import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { siteConfig } from "../../../../settings";
import { getFileName } from "../../../../helpers/helper";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;

    const column = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code"
        // render: text => {
        //   if (text && text.length > 10) {
        //     return text.substring(0, 10) + "...";
        //   } else {
        //     return text;
        //   }
        // }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 25) {
            return text.substring(0, 25) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "poQty" }),
        dataIndex: "ordered_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "poBaseQty" }),
        dataIndex: "base_ordered_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "rcvdQty" }),
        dataIndex: "received_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "leftQty" }),
        dataIndex: "left_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "receiveQty" }),
        dataIndex: "receive_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "baseQty" }),
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      }
    ];

    const leftSide = [
      {
        label: <IntlMessages id="purchaseOrders.purchaseOrderNo" />,
        description: data.purchase_order_no
      },
      {
        label: <IntlMessages id="purchaseReceipts.supplierDeliveryNo" />,
        description: data.supplier_delivery_no
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="itemTransfers.courier" />,
        description: data.courier
          ? `${data.courier.courier_name} (${data.courier.description})`
          : null
      },
      {
        label: <IntlMessages id="purchaseReceipts.location" />,
        description: data.receipt_location
          ? data.receipt_location.location_name
          : null
      }
    ];

    const total = [
      {
        label: (
          <>
            <IntlMessages id="purchaseReceipts.totalQty" />
            <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
          </>
        ),
        amount: <NumberFormat value={data.total_qty} />
        // parseAmount: true
      },
      {
        label: (
          <>
            <IntlMessages id="purchaseReceipts.totalReceiveQty" />
            <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
          </>
        ),
        amount: <NumberFormat value={data.total_qty_receipt} />
        // parseAmount: true
      },
      {
        label: (
          <>
            <IntlMessages id="purchaseReceipts.totalLeftQty" />
            <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
          </>
        ),
        amount: (
          <NumberFormat
            value={
              parseFloat(data.total_qty) - parseFloat(data.total_qty_receipt)
            }
          />
        )
        // parseAmount: true
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.purchaseReceipts" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.purchase_receipt_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.purchase_receipt_date
                        ? moment(
                            data.purchase_receipt_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          <Row>
            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="sidebar.supplier" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={13}>
                  <strong>
                    {data.supplier ? data.supplier.supplier_name : null}
                  </strong>
                </Col>
              </Row>
            </Col>

            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={15}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row>

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="purchaseInvoices.tabs.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={data.purchase_receipt_details}
                rowKey={row => row.id_purchase_receipt_detail}
                pagination={false}
              />
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col push={12} span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "purchase_receipt",
              data.purchase_receipt_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
