import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/purchase/purchaseReceipt/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import {
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getTransactions } = actions;

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%"
      // fixed: "left"
    },
    {
      title: getLanguage({ id: "code" }),
      dataIndex: "item_code",
      width: "10%",
      // fixed: "left",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "15%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "quantity" }),
      dataIndex: "qty",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "unit" }),
      width: "10%",
      dataIndex: "unit.unit_code",
      align: "center"
    },
    {
      title: getLanguage({ id: "price" }),
      dataIndex: "item_price",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "tax" }),
      dataIndex: "tax.tax_name",
      width: "10%",
      align: "center"
    },
    {
      title: getLanguage({ id: "discount" }),
      dataIndex: "discount",
      width: "10%",
      align: "right",
      render: text => {
        if (text.slice(-1) === "%") {
          return text;
        } else {
          return (<NumberFormat value={text} />);
        }
      }
    },
    {
      title: getLanguage({ id: "subTotal" }),
      dataIndex: "sub_total",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "netPurchasePrice" }),
      dataIndex: "netto_purchase_price",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalTransaction extends Component {
  state = {
    filter: {
      id_supplier: this.props.id_supplier,
      id_location: this.props.id_location,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  componentDidUpdate(prevProps) {
    const { id_location, id_supplier } = this.props;

    if (
      prevProps.id_location !== id_location &&
      prevProps.id_supplier !== id_supplier &&
      id_location &&
      id_supplier
    ) {
      this.handleLocSupChange(id_location, id_supplier);
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_location: id_location
        }
      });
    } else if (
      prevProps.id_location !== id_location &&
      id_location &&
      id_supplier
    ) {
      this.handleLocSupChange(id_location, id_supplier);
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_location: id_location
        }
      });
    } else if (
      prevProps.id_supplier !== id_supplier &&
      id_supplier &&
      id_location
    ) {
      this.handleLocSupChange(id_location, id_supplier);
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: id_supplier,
          id_location: id_location
        }
      });
    }
  }

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  // Handle search
  // handleSearch = value => {
  //   const { getTransactions } = this.props;
  //   getTransactions(1, value);
  // };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    this.setState({
      filter: {
        id_supplier: this.props.id_supplier,
        id_location: this.props.id_location,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  handleLocSupChange = (id_location, id_supplier) => {
    const { getTransactions, transaction_date } = this.props;
    let string = 
      `id_location=${id_location}&id_supplier=${id_supplier}&transaction_date=${transaction_date}`;
    getTransactions(1, null, string);
  };

  handleLocationChange = id_location => {
    const { getTransactions } = this.props;
    const { filter } = this.state;

    let string = "id_location=" + id_location;

    if (filter.id_supplier) {
      string += "&id_supplier=" + filter.id_supplier;
    }

    getTransactions(1, null, string);
  };

  handleSupplierChange = id_supplier => {
    const { getTransactions } = this.props;
    const { filter } = this.state;

    let string = "id_supplier=" + id_supplier;

    if (filter.id_location) {
      string += "&id_location=" + filter.id_location;
    }

    getTransactions(1, null, string);
  };

  // Handle filter
  handleFilter = () => {
    const { getTransactions, transaction_date } = this.props;
    //const { current_page } = this.props.customer;
    const { filter, search } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
          "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
          "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    } else {
      string
        ? (string +=
            "&transaction_date=" + transaction_date)
        : (string +=
            "transaction_date=" + transaction_date);
    }
    if (search) {
      string
        ? (string += "&search=" + search)
        : (string += "search=" + search);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    // console.log(string);
    // this.setState({ search: null });
    getTransactions(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    this.setState({
      filter: {
        id_supplier: this.props.id_supplier,
        id_location: this.props.id_location,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  // Handle pagination
  handlePageChange = page => {
    const { getTransactions } = this.props;
    const { search, filter } = this.props.purchaseReceipt;
    getTransactions(page, search, filter);
  };

  render() {
    // console.log(this.props);
    const { visible, handleModal, purchaseReceipt, transaction_date } = this.props;
    const { transactions, locations, suppliers } = purchaseReceipt
      ? purchaseReceipt
      : [];
    const {
      loadingSuppliers,
      loadingLocations,
      loadingTransactions
    } = purchaseReceipt ? purchaseReceipt : false;

    const columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "purchase_order_no",
        rowKey: "purchase_order_no",
        width: "25%",
        render: (text, row) => {
          let className;
          switch (row.transaction_status) {
            case 4:
              className = "new";
              break;
            case 1:
              className = "ordered";
              break;
            case 2:
              className = "received";
              break;
            case 3:
              className = "closed";
              break;
            case 5:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_purchase_order);
                  }}
                  href="javascript:;"
                >
                  <TransNo>{text}</TransNo>
                </a>
              </Tooltip>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.supplier" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                <a href="javascript:;">{text.substring(0, 25) + "..."}</a>
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                <a href="javascript:;">{text.substring(0, 25) + "..."}</a>
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                <a href="javascript:;">{text.substring(0, 25) + "..."}</a>
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "transaction_status",
        rowKey: "transaction_status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 4:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 1:
              className = "ordered";
              display = getLanguage({ id: "ordered" });
              break;
            case 2:
              className = "received";
              display = getLanguage({ id: "received" });
              break;
            case 3:
              className = "closed";
              display = getLanguage({ id: "closed" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const dataSource = [];
    Object.keys(transactions).map((item, index) => {
      return dataSource.push({
        ...transactions[item],
        key: item
      });
    });

    const { current_page, page_size, total } = this.props.purchaseReceipt
      ? this.props.purchaseReceipt
      : [];

    return (
      <Modal
        title={<IntlMessages id="purchaseReceipts.importPO" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
      >
        <Row style={{ marginBottom: "20px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                `${getLanguage({ id: "transactionNo" })}, ` +
                `${getLanguage({ id: "createdBy" })}`
              }
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleFilter}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "20px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row type="flex" style={{ marginBottom: "24px" }}>
                  <Input.Group>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="suppliers.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_supplier"
                        )}
                        value={
                          this.state.filter.id_supplier
                            ? this.state.filter.id_supplier
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.filter.select.supplier" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingSuppliers}
                        allowClear={true}
                        disabled={true}
                      //showArrow={false}
                      >
                        {suppliers.map((data, key) => (
                          <Option key={key} value={data.id_supplier}>
                            {`${data.supplier_code} (${data.supplier_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="locations.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_location"
                        )}
                        value={
                          this.state.filter.id_location
                            ? this.state.filter.id_location
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.filter.select.location" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingLocations}
                        allowClear={true}
                        disabled={true}
                      //showArrow={false}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                  </Input.Group>
                </Row>
                <Row type="flex" justify="start">
                  <Input.Group>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="purchaseReceipts.filter.date" />
                      </label>
                      <br />
                      <RangePicker
                        showTime
                        format="DD MMMM YYYY HH:mm:ss"
                        allowClear={true}
                        style={{ width: "100%" }}
                        value={
                          this.state.filter.start_date &&
                            this.state.filter.end_date
                            ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                            : []
                        }
                        onChange={this.handleFilterChange.bind(this, "date")}
                        disabledDate={current =>
                          current > moment(transaction_date).endOf("day")
                        }
                      />
                    </Col>

                    <Col span={12}>
                      <Row gutter={1} type="flex">
                        <Col span={6}>
                          <label>
                            <IntlMessages id="dataPerPage" />
                          </label>
                          <br />
                          <Select
                            showSearch
                            onChange={this.handleFilterChange.bind(
                              this,
                              "pagination"
                            )}
                            value={
                              this.state.filter.pagination
                                ? this.state.filter.pagination
                                : page_size
                            }
                            style={{ width: "80%" }}
                            // placeholder={
                            //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                            // }
                            optionFilterProp="children"
                            filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                          >
                            <Option key={1} value={10}>
                              10
                            </Option>
                            <Option key={2} value={25}>
                              25
                            </Option>
                            <Option key={3} value={50}>
                              50
                            </Option>
                            <Option key={4} value={100}>
                              100
                            </Option>
                          </Select>
                        </Col>
                        <Col
                          span={6}
                          style={{
                            marginTop: "auto"
                          }}
                        >
                          <Button
                            size="small"
                            type="primary"
                            style={{ marginRight: "10px" }}
                            onClick={this.handleFilter}
                          >
                            <IntlMessages id="filter.submit" />
                          </Button>
                          <Button onClick={this.handleResetFilter} size="small">
                            <IntlMessages id="filter.reset" />
                          </Button>
                        </Col>
                      </Row>
                    </Col>
                  </Input.Group>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>
        <Row>
          <Col>
            {dataSource.length === 0 && !loadingTransactions ? (
              <Empty
                description={<IntlMessages id="purchaseOrders.table.noData" />}
              />
            ) : (
                <Table
                  dataSource={dataSource}
                  columns={columns}
                  loading={loadingTransactions}
                  expandedRowRender={record =>
                    expandedRowRender(record.purchase_order_details)
                  }
                  expandIcon={props => (
                    <Button
                      onClick={e => props.onExpand(props.record, e)}
                      size="small"
                    >
                      <IntlMessages id="button.view" />
                    </Button>
                  )}
                  // Set pagination, jika ada page_size mengunakan pagination API
                  // jika tidak ada page_size menggunakan pagination bawaan
                  pagination={
                    page_size
                      ? {
                        current: current_page,
                        pageSize: parseInt(page_size),
                        total: total,
                        onChange: this.handlePageChange.bind(this)
                      }
                      : true
                  }
                />
              )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getTransactions }
  )
)(ModalTransaction);
